.section-banner__container {
  position: relative;
  max-width: 1920px;
}
.section-banner__container::before {
  content: "";
  display: block;
  padding-top: 34.1482047364%;
}
.section-banner__img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.section-banner__content {
  position: absolute;
  width: 80%;
  top: 55px;
  left: 190px;
}
@media (max-width: 575px) {
  .section-banner__content {
    top: 13px;
    left: 55px;
  }
}
.section-banner__content-container {
  position: relative;
  max-width: 250px;
}
.section-banner__content-container::before {
  content: "";
  display: block;
  padding-top: 100%;
}
.section-banner__content-img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.section-banner__date-created {
  color: #6DDA5F;
}
.section-banner__desc {
  display: -webkit-box;
  line-height: 1.5;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}

.swiper {
  margin-left: 0px !important;
  width: 80%;
  max-width: 1000px;
}
.swiper-pagination-bullets {
  bottom: 0px !important;
}
.swiper-pagination-bullet {
  background: none !important;
  border: 1px solid #03A84E !important;
}
.swiper-pagination-bullet-active {
  background: #6DDA5F !important;
}

.form-group label {
  font-size: 16px !important;
}

.bootstrap-tagsinput {
  display: flex;
  cursor: default;
  background-color: transparent;
  border: none;
  box-shadow: none;
}
.bootstrap-tagsinput input {
  display: none;
}

.section-blog__label {
  display: flex;
  padding: 10px !important;
  justify-content: space-between;
  align-items: center;
  font-size: 16px;
  background-color: #6DDA5F;
}
.section-blog__label i {
  cursor: pointer;
}
.section-blog__btn-sidebar {
  background-color: #6DDA5F;
}